import {ScrollTrigger} from './../../vendor/scroll-trigger.min.js';
import {locomotive} from './init-locomotive';
let scrollTrigger;
let pageScroller;

const setScrollTrigger = () => {
  scrollTrigger = ScrollTrigger;
  pageScroller = scrollTrigger.create({
    trigger: '[data-scroll-container]',
    scroller: '[data-scroll-container]',
    start: 'top top',
    end: 'bottom bottom',
    onUpdate: (self) => {
      // console.log(self.progress)
    }
  });
  window.ps = pageScroller;
}

const initScrollTrigger = () => {
  gsap.registerPlugin(ScrollTrigger);

  if (!locomotive) {
    // console.warn('LocomotiveScroll not initialized');
    setScrollTrigger();
    return;
  }
  // –ø—Ä–∏ –∫–∞–∂–¥–æ–º –æ–±–Ω–æ–≤–ª–µ–Ω–∏–∏ –ª–æ–∫–æ–º–æ—Ç–∏–≤ –±—É–¥–µ—Ç –¥–µ—Ä–≥–∞—Ç—å ScrollTrigger (—Å–∏–Ω—Ö—Ä–æ–Ω–∏–∑–∏—Ä—É–µ–º –∏—Ö)
  locomotive.on('scroll', ScrollTrigger.update);

  // —á–µ—Ä–µ–∑ scrollerProxy –ø—Ä–æ—Å–∏–º ScrollTrigger –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å –∏–≤–µ–Ω—Ç—ã –ª–æ–∫–æ–º–æ—Ç–∏–≤–∞ –≤–º–µ—Å—Ç–æ –∏–≤–µ–Ω—Ç–æ–≤ —Å–∫—Ä–æ–ª–ª–∞ js, –ø–æ–∫–∞ –ª–æ–∫–æ–º–æ—Ç–∏–≤ —Ä–∞–±–æ—Ç–∞–µ—Ç
  ScrollTrigger.scrollerProxy('[data-scroll-container]', {
    scrollTop(value) {
      return arguments.length ? locomotive.scrollTo(value, 0, 0) : locomotive.scroll.instance.scroll.y;
    }, // –Ω–∞—Å—Ç—Ä–∞–∏–≤–∞–µ–º —Ç–æ–ª—å–∫–æ –ø–æ –æ—Å–∏ Y –¥–ª—è –≤–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–æ–≥–æ —Å–∫—Ä–æ–ª–ª–∞
    getBoundingClientRect() {
      return {top: 0, left: 0, width: window.innerWidth, height: window.innerHeight};
    },
    // —Ö–∞–∫ –¥–ª—è –ª–æ–∫–æ–º–æ—Ç–∏–≤–∞
    pinType: document.querySelector('[data-scroll-container]').style.transform ? 'transform' : 'fixed',
  });

  // —Å–≤—è–∑—ã–≤–∞–µ–º –ª–æ–∫–æ–º–æ—Ç–∏–≤ –∏ —Å–∫—Ä–æ–ª–ª—Ç—Ä–∏–≥–µ—Ä
  ScrollTrigger.addEventListener('refresh', () => locomotive.update());
  ScrollTrigger.refresh();

  window.addEventListener('scrolltrigger.update', () => {
    ScrollTrigger.refresh();
  });

  setScrollTrigger();
};

export {initScrollTrigger, scrollTrigger, pageScroller};
